import React, { useState } from "react";
import "./IndicatorsTable.less";
import Modal from "../Modal";
import ModalCard from "../ModalCard/ModalCard";
import { useSelector } from "react-redux";
import { getPagStart, getPagEnd } from "../../store/pagination";

export default function IndicatorsTable({ activeTh, cards }) {

    const [modalActive, setModalActive] = useState();
    const [activeCardId, setActiveCardId] = useState();

    const pagStart = useSelector(getPagStart);
    const pagEnd = useSelector(getPagEnd);

    return (
        <>
            <div className="u-table">
                <table className="table indicators-table tt">
                    <thead>
                        <tr>
                            <th>
                                <p>№</p>
                            </th>

                            {activeTh.title ?
                                <th>
                                    <p>Показатель</p>
                                </th> : <></>}

                            {activeTh.kindActivity ?
                                <th>
                                    <p>Вид деятельности</p>
                                </th> : <></>}

                            {activeTh.limitation ?
                                <th>
                                    <p>Ограничение</p>
                                </th> : <></>}

                            {activeTh.points ?
                                <th >
                                    <p>Баллы</p>
                                </th> : <></>}

                            {activeTh.points_with_limits ?
                                <th >
                                    <p>Баллы, с учетом ограничений</p>
                                </th> : <></>}

                            {activeTh.transfer_points ?
                                <th >
                                    <p>Передача баллов</p>
                                </th> : <></>}

                            {activeTh.amount ?
                                <th >
                                    <p>К оплате</p>
                                </th> : <></>}
                        </tr>
                    </thead>
                    <tbody>
                        {
                            cards
                                .map((card, num) => {
                                    return (
                                        <tr key={card.id}>
                                            <td>
                                                <div style={{ width: "12px", whiteSpace: "nowrap" }}>{num + 1}</div>
                                            </td>

                                            {activeTh.title ?
                                                <td>
                                                    <a style={{ width: "455px" }} className="indicators-table__card-title clr-blue-main"
                                                        onClick={() => {
                                                            setModalActive(true);
                                                            setActiveCardId(card.id);
                                                        }}
                                                    >{card.title}</a>
                                                </td> : <></>}

                                            {activeTh.kindActivity ?
                                                <td>
                                                    <div style={{ width: "187px" }}>{card.kindActivity}</div>
                                                </td> : <></>}

                                            {activeTh.limitation ?
                                                <td>
                                                    <div style={{ width: "107px" }}>{card.limitation}</div>
                                                </td> : <></>}

                                            {activeTh.points ?
                                                <td>
                                                    <div style={{ width: "67px" }}>{card.points}</div>
                                                </td> : <></>}

                                            {activeTh.points_with_limits ?
                                                <td>
                                                    <div style={{ width: "140px" }}>{card.points_with_limits}</div>
                                                </td> : <></>}
                                            {activeTh.transfer_points ?
                                                <td>
                                                    <div style={{ width: "107px" }}>{card.transfer_points || "-"}</div>
                                                </td> : <></>}

                                            {activeTh.amount ?
                                                <td>
                                                    <div style={{ width: "70px" }}>{card.amount}</div>
                                                </td> : <></>}
                                        </tr>
                                    );
                                })
                                .slice(pagStart, pagEnd)
                        }
                    </tbody>
                </table>
            </div>
            <Modal active={modalActive} setActive={setModalActive}>
                <ModalCard activeCardId={activeCardId} cards={cards}></ModalCard>
            </Modal>
        </>
    );
}