import React, { useEffect, useState } from "react";
import "./ModalCard.less";
import Selectbox from "../Selectbox";
import FactualValues from "../FactualValues/FactualValues";
import AirDatepicker from 'air-datepicker';
import 'air-datepicker/air-datepicker.css';

export default function ModalCard({ activeCardId, cards }) {
    const [addValue, setAddValue] = useState(false);
    const [isSave, setIsSave] = useState(false);
    const [approving, setApproving] = useState(false);

    const card = cards.find((card) => card.id === activeCardId);

    let dp = new AirDatepicker('#date', {
        position: 'bottom right'
    });

    window.addEventListener('scroll', () => {
        if (dp.visible) {
            dp.hide();
        }
    })

    useEffect(() => {
        document.querySelector(".u-modal").addEventListener('scroll', () => {
            if (dp.visible) {
                dp.hide();
            }
        })
    }, [])

    useEffect(() => {
        if (isSave == false) {
            setApproving(false);
        }
    }, [isSave])

    return (
        <div className="modalcard">

            <h3 className="modalcard__title clr-blue-main">{card?.title}</h3>

            <div className="modalcard__data tt">
                <p className="modalcard__data-col1">
                    К оплате: {card?.amount} <br />
                    Баллы: {card?.points} <br />
                    Баллы, с учетом ограничений: {card?.points_with_limits} <br />
                </p>
                <p>
                    Ограничение: {card?.limitation} <br />
                    Передача баллов: {card?.transfer_points || "-"}
                </p>
            </div>

            <div className="modalcard_mb clr-blue-main">
                Информация по показателю
            </div>
            <h5 className="modalcard_mb">{card?.info}</h5>

            <div className="modalcard_mb clr-blue-main">
                Методика расчета баллов за показатель
            </div>

            <h5 className="modalcard_mb">{card?.calculation}</h5>

            <div className={card?.isEditable ? "modalcard_open" : "modalcard_hide"}>

                <div className="modalcard-factual">
                    <h3 className="modalcard-factual__heading clr-blue-main">Фактические значения</h3>
                    <div className={addValue ? "modalcard_open" : "modalcard_hide"}>
                        <div className="modalcard-factual-flex">
                            <p className="modalcard-factual__p">Значение №{(card?.factual?.length || 0) + 1}</p>
                            <div className="u-status u-status-large u-status-neutral">Не отправлено</div>
                        </div>

                        {/* <div className="u-message-fail modalcard-factual-message">Комментарий администратора: <br />Причина отказа – поля карточки не заполнены.</div> */}

                        <div className="modalcard-factual__inputs">

                            <div className="modalcard-factual-grid modalcard-factual-grid-1">
                                <div>
                                    <label htmlFor="worker" className="modalcard-factual__label">Работник</label>
                                    <input placeholder="Не указано" className="u-input" type="text" id="worker" />
                                </div>
                                <div>
                                    <label htmlFor="date" className="modalcard-factual__label">Дата проведения</label>
                                    <input type="text" id="date" className="u-calendar" />
                                </div>
                            </div>

                            <div className="modalcard-factual-grid modalcard-factual-grid-2">
                                <div>
                                    <label htmlFor="link" className="modalcard-factual__label">Ссылка на мероприятие</label>
                                    <input placeholder="Ссылка" className="u-input" type="text" name="" id="link" />
                                </div>
                                <div>
                                    <label htmlFor="issn" className="modalcard-factual__label">ISSN/eISSN</label>
                                    <input placeholder="1234-567Х" className="u-input" type="text" name="" id="issn" />
                                </div>
                            </div>

                            <div className="modalcard-factual-grid modalcard-factual-grid-3">
                                <div className="modalcard-factual-select1">
                                    <label htmlFor="status" className="modalcard-factual__label">Статус СМИ</label>
                                    <Selectbox optionsArr={["Федеральное и международное СМИ", "Региональное СМИ", "Муниципальное СМИ"]} placeholder="Не указано" />
                                </div>
                                <div className="modalcard-factual-select2">
                                    <label htmlFor="view" className="modalcard-factual__label">Вид СМИ</label>
                                    <Selectbox optionsArr={["Статья, выступление на мероприятии, на телевидении, интервью на радио, в интернете, печатных СМИ", "Выступление на радио, комментарии в интернете, печатных СМИ"]} placeholder="Не указано" />
                                </div>
                            </div>
                        </div>

                        <div className="modalcard-factual-flex-space-between modalcard-factual__buttons">

                            <div className="modalcard-factual-flex">
                                <button className="u-button u-button-simple"
                                    onClick={() => {
                                        setAddValue(false);
                                    }}>Отмена</button>

                                <button className="u-button u-button-outline modalcard-factual__button-save"
                                    onClick={() => {
                                        setAddValue(false);
                                        setIsSave(true);
                                    }}>Сохранить</button>
                            </div>

                            <button className="u-button modalcard-factual__button-agreement"
                                onClick={() => {
                                    setAddValue(false);
                                    setIsSave(true);
                                    setApproving(true);
                                }}>
                                Отправить на согласование
                                <i className="u-icon icon-like"></i>
                            </button>

                        </div>
                    </div>
                </div>

                <div className={isSave && !addValue ? "modalcard_open" : "modalcard_hide"}>
                    <div className="modalcard-factual__stored-value">
                        <div className="modalcard-factual-flex-space-between">
                            <div className="modalcard-factual-flex">
                                <p className="modalcard-factual__p modalcard-factual__p_blue clr-blue-main"
                                    onClick={() => {
                                        setAddValue(true);
                                    }}>Значение №{(card?.factual?.length || 0) + 1}</p>
                                {approving ? <div className="u-status u-status-large u-status-warning">На согласовании</div> :
                                    <div className="u-status u-status-large u-status-neutral">Не отправлено</div>}
                            </div>
                            <button className="u-button-text u-button-text-danger"
                                onClick={() => {
                                    setIsSave(false);
                                }}>
                                <i className="u-icon icon-cross modalcard-factual-icon"></i>
                            </button>
                        </div>
                    </div>
                </div>

                <FactualValues card={card} isModal={true} />

                <button className="u-button u-button-small modalcard-factual__button-add"
                    onClick={() => {
                        setAddValue(!addValue);
                    }}>
                    Добавить фактическое значение <i className="u-icon icon-plus"></i>
                </button>
            </div>
        </div>
    );
}