import React from "react";
import "./FactualValues.less";

export default function FactualValues({ card, isModal }) {

    let statuses = new Map();
    statuses.set("Не отправлено", "u-status-neutral");
    statuses.set("Согласовано", "u-status-success");
    statuses.set("На согласовании", "u-status-warning");
    statuses.set("Отклонено", "u-status-danger");

    return (
        <>
            {
                card?.isEditable ?
                    card?.factual?.map((value, index) => {
                        if (isModal) {
                            return (
                                <div className="factual-values-modal" key={value.id}>
                                    <div className="factual-values-flex-space-between">
                                        <div className="factual-values-flex">
                                            <p className="factual-values-modal__p factual-values-modal__p_blue clr-blue-main">Значение №{card.factual.length - index}</p>
                                            <div className={"u-status u-status-large " + statuses.get(value.status)}>{value.status}</div>
                                        </div>
                                        <button className="u-button-text u-button-text-danger">
                                            <i className="u-icon icon-only icon-cross factual-values-modal-icon"></i>
                                        </button>
                                    </div>
                                </div>
                            )
                        }
                        else {
                            return (
                                <div className="factual-values__card factual-values__card-agreement bg-white" key={value.id}>
                                    <div className="factual-values-flex-space-between">
                                        <div className="factual-values-flex">
                                            <p className="factual-values__p factual-values__p_blue clr-blue-main">Значение №{card.factual.length - index}</p>
                                            <div className={"u-status u-status-large " + statuses.get(value.status)} id="status">{value.status}</div>
                                        </div>
                                        <button className="u-button-text u-button-text-danger u-button-text-icon-left">
                                            Удалить<i className="u-icon icon-basket factual-values-icon"></i>
                                        </button>
                                    </div>
                                </div>
                            )
                        }
                    }) : <></>
            }
        </>
    )
}