import React, { useState } from "react";
import { Link, useParams } from "react-router-dom";
import "./IndicatorCard.less";
import { cardsApi } from "../cardsApi";
import Selectbox from "../Selectbox";
import { path } from "../../../router";
import { useSelector } from "react-redux";
import { getActiveView } from "../../store/location";
import { getPeriodId } from "../../store/period";
import FactualValues from "../FactualValues/FactualValues";
import AirDatepicker from 'air-datepicker';
import 'air-datepicker/air-datepicker.css';
import { useEffect } from "react";

export default function IndicatorCard() {
    const [otherElems, setOtherElems] = useState(false);
    const [info, setInfo] = useState(true);
    const [calculation, setCalculation] = useState(true);
    const [addValue, setAddValue] = useState(false);
    const [isSave, setIsSave] = useState(false);

    const { cardId } = useParams();
    const periodId = useSelector(getPeriodId);
    const [card, setCard] = useState(cardsApi[periodId].cards);

    useEffect(() => {
        setCard(cardsApi[periodId].cards.find((card) => card?.id === cardId))
    }, [periodId, cardId])

    const view = useSelector(getActiveView);

    let dp = new AirDatepicker('#date', {
        position: 'bottom right'
    });

    window.addEventListener('scroll', () => {
        if (dp.visible) {
            dp.hide();
        }
    })

    return (
        <div className="indicatorcard">

            <div className="u-bc">
                <Link to={`${path.npr.indicators}/${view}`}
                    className="u-bc-link">Главная</Link>
                <Link to={`${path.npr.indicators}/${view}`} className="u-bc-link">Система стимулирования НПР</Link>
                <a className="u-bc-last">Карточка показателя</a>
            </div>

            <h2 className="indicatorcard__h2 clr-blue-main">Система стимулирования НПР</h2>

            <p className="indicatorcard__reporting-period tt clr-blue-main">
                Отчетный период:
                <span className="indicatorcard__reporting-period-span">{card?.period}</span>
            </p>

            <div className="indicatorcard__card bg-white">

                <p className="indicatorcard__card-title clr-blue-main">{card?.title}</p>

                <div className="flex-container indicatorcard__card-content">

                    <div>
                        <div className="indicatorcard__section-title clr-blue-main"
                            onClick={() => {
                                setInfo(!info);
                            }}>
                            Информация по показателю
                            <i className={info
                                ? "u-icon icon-up indicatorcard-icon"
                                : "u-icon icon-down indicatorcard-icon"}></i>
                        </div>

                        <div className={info ? "indicatorcard_open" : "indicatorcard_hide"}>
                            <p className="tt indicatorcard__section-mb">{card?.info}</p>
                        </div>

                        <div className="indicatorcard__section-title clr-blue-main"
                            onClick={() => {
                                setCalculation(!calculation);
                            }}>
                            Методика расчета баллов за показатель
                            <i className={calculation
                                ? "u-icon icon-up indicatorcard-icon"
                                : "u-icon icon-down indicatorcard-icon"}></i>
                        </div>

                        <div className={calculation ? "indicatorcard_open" : "indicatorcard_hide"}>
                            <p className="tt">{card?.calculation}</p>
                        </div>

                    </div>

                    <div className="indicatorcard__col">

                        <p className="indicatorcard__data">
                            К оплате: {card?.amount} <br />
                            Баллы: {card?.points} <br />
                            Ограничение: {card?.limitation}
                        </p>

                        <div className="indicatorcard__other-elements">

                            <div className={otherElems ? "indicatorcard__other-elements-heading clr-blue-main" : "indicatorcard__other-elements-heading clr-gray-60"}
                                onClick={() => {
                                    setOtherElems(!otherElems);
                                }}>
                                Другие элементы
                                <i className={otherElems
                                    ? "u-icon icon-up indicatorcard-icon"
                                    : "u-icon icon-down indicatorcard-icon indicatorcard__other-elements-inactive"}></i>
                            </div>

                            <div className={otherElems ? "indicatorcard_open" : "indicatorcard_hide"}>
                                <div className="indicatorcard__other-elements-text">Баллы, с учетом ограничений: {card?.points_with_limits}</div>
                                <div className="indicatorcard__other-elements-text">Передача баллов: {card?.transfer_points || "-"}</div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>

            <div className={card?.isEditable ? "indicatorcard_open" : "indicatorcard_hide"}>

                <div className="indicatorcard-factual">

                    <div className="indicatorcard-factual__heading clr-blue-main">

                        <p className="indicatorcard__card-title">Фактические значения</p>

                        <button className="u-button"
                            onClick={() => {
                                setAddValue(!addValue);
                            }}>
                            Добавить значение <i className="u-icon icon-plus"></i>
                        </button>
                    </div>

                    <div className={addValue ? "indicatorcard_open" : "indicatorcard_hide"}>

                        <div className="indicatorcard__card indicatorcard__card-factual bg-white">

                            <div className="indicatorcard-factual-flex">
                                <p className="indicatorcard-factual__p">Значение № {card?.factual?.length || 0 + 1} </p>
                                <div className="u-status u-status-large u-status-neutral">Не отправлено</div>
                            </div>

                            <div className="indicatorcard-factual__inputs">
                                <div className="indicatorcard-factual-grid indicatorcard-factual-grid-1">

                                    <div>
                                        <label htmlFor="worker" className="indicatorcard-factual__label">Работник</label>
                                        <input placeholder="Не указано" className="u-input" type="text" name="" id="worker" />
                                    </div>
                                    <div>
                                        <label htmlFor="date" className="indicatorcard-factual__label">Дата проведения</label>
                                        <input type="text" id="date" className="u-calendar" />
                                    </div>
                                </div>

                                <div className="indicatorcard-factual-grid indicatorcard-factual-grid-2">

                                    <div>
                                        <label htmlFor="link" className="indicatorcard-factual__label">Ссылка на мероприятие</label>
                                        <input placeholder="Ссылка" className="u-input" type="text" name="" id="link" />
                                    </div>
                                    <div>
                                        <label htmlFor="issn" className="indicatorcard-factual__label">ISSN/eISSN</label>
                                        <input placeholder="1234-567Х" className="u-input" type="text" name="" id="issn" />
                                    </div>
                                </div>

                                <div className="indicatorcard-factual-grid indicatorcard-factual-grid-3">

                                    <div className="indicatorcard-factual-select1">
                                        <label htmlFor="status" className="indicatorcard-factual__label">Статус СМИ</label>
                                        <Selectbox optionsArr={["Федеральное и международное СМИ", "Региональное СМИ", "Муниципальное СМИ"]} placeholder="Не указано" />
                                    </div>
                                    <div className="indicatorcard-factual-select2">
                                        <label htmlFor="view" className="indicatorcard-factual__label">Вид СМИ</label>
                                        <Selectbox optionsArr={["Статья, выступление на мероприятии, на телевидении, интервью на радио, в интернете, печатных СМИ", "Выступление на радио, комментарии в интернете, печатных СМИ"]} placeholder="Не указано" />
                                    </div>
                                </div>

                            </div>

                            <div className="indicatorcard-factual-flex-space-between">

                                <div className="indicatorcard-factual-flex">
                                    <button className="u-button u-button-simple"
                                        onClick={() => {
                                            setAddValue(false);
                                        }}>Отмена</button>
                                    <button className="u-button u-button-outline indicatorcard-factual-button-save"
                                        onClick={() => {
                                            setAddValue(false);
                                            setIsSave(true);
                                        }}>Сохранить</button>
                                </div>
                                <button className="u-button indicatorcard-factual-button-agreement"
                                    onClick={() => {
                                        setAddValue(false);
                                    }}>
                                    Отправить на согласование
                                    <i className="u-icon icon-like"></i>
                                </button>
                            </div>
                        </div>
                    </div>
                </div>

                <div className={isSave && !addValue ? "indicatorcard_open" : "indicatorcard_hide"}>
                    <div className="indicatorcard__card indicatorcard__card-agreement bg-white">
                        <div className="indicatorcard-factual-flex-space-between">
                            <div className="indicatorcard-factual-flex">
                                <p className="indicatorcard-factual__p indicatorcard-factual__p_blue clr-blue-main"
                                    onClick={() => {
                                        setAddValue(true);
                                    }}>Значение №{card?.factual?.length + 1}</p>
                                <div className="u-status u-status-large u-status-neutral">Не отправлено</div>
                            </div>
                            <button className="u-button-text u-button-text-danger u-button-text-icon-left"
                                onClick={() => {
                                    setIsSave(false);
                                }}>
                                Удалить<i className="u-icon icon-basket indicatorcard-factual-icon"></i>
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            <FactualValues card={card} isModal={false} />

        </div>
    );
}