import { CHANGE_ACTIVE_TAB, CHANGE_ACTIVE_VIEW } from "./actions";
import { path } from "../../../router";

export const locationInitialState = {
    tab: `${path.npr.indicators}`.split("/").pop(),
    view: `${path.npr.indicatorsTable}`.split("/").pop()
}

export const locationReducer = (state = locationInitialState, action) => {
    switch (action.type) {
        case CHANGE_ACTIVE_TAB: return {
            ...state,
            tab: action.payload
        }
        case CHANGE_ACTIVE_VIEW: return {
            ...state,
            view: action.payload
        }
        default: { return state; }
    }
}