import React from "react";
import "./Documentation.less";
import { docs } from "./docs.js";

export default function Documentation() {
    return (
        <div className="documentation bg-white">
            {
                docs.map((doc, id) => {
                    return (
                        <div className="documentation__doc" key={id}>
                            <i className={doc.extension === "pdf"
                                ? "u-icon icon-pdf documentation-icon clr-red-main"
                                : doc.extension === "doc"
                                    ? "u-icon icon-doc documentation-icon clr-blue-main"
                                    : ""}></i>
                            <a href={require(`../../${doc.url}`)} target="_blank" className="tt documentation__doc-name">{doc.name}</a>
                        </div>
                    )
                })
            }
        </div>
    );
}