import React from "react";
import { HashRouter, Route } from "react-router-dom";
import NPR from "./NPR/NPR";
import IndicatorCard from "./IndicatorCard/IndicatorCard";
import { path } from "../../router";
import PageContainer from "../components/PageContainer/PageContainer";

export default function Router() {
    return (
        <HashRouter>
            <Route exact path={[`${path.main}`, `${path.npr.main}`, `${path.npr.params}`, `${path.npr.indicatorsParams}`]}>
                <PageContainer>
                    <NPR />
                </PageContainer>
            </Route>
            <Route path={[`${path.npr.indicatorId}`]}>
                <PageContainer>
                    <IndicatorCard />
                </PageContainer>
            </Route>
            <Route path={`${path.ykvd}`}>
                <PageContainer>
                    <div>Сервис "Эффективный контракт высших достижений"</div>
                </PageContainer>
            </Route>
        </HashRouter>
    );
}
