  /*для средних планшетов*/
  h2 {
    font-size: 24px;
    line-height: 28px;
  }
  h3 {
    font-size: 16px;
    line-height: 20px;
  }
  h4 {
    font-size: 14px;
    line-height: 18px;
  }
}
/*media queries*/
.factual-values {
  margin-bottom: 6px;
}
.factual-values__card {
  border-radius: 10px;
  padding: 50px 32px 32px 50px;
  max-width: 1012px;
  margin-top: 32px;
}
.factual-values__card-agreement {
  margin-top: 16px;
  padding: 26px 32px;
}
.factual-values__heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 26px;
  margin-bottom: 20px;
}
.factual-values-flex {
  display: flex;
  align-items: center;
}
.factual-values-flex-space-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.factual-values-icon {
  font-size: 20px;
  margin-left: 10px;
  cursor: pointer;
}
.factual-values__p {
  font-size: 24px;
  font-weight: 400;
  margin-right: 10px;
}
.factual-values__p_blue {
  margin-right: 16px;
  cursor: pointer;
}
.factual-values__p_blue:hover {
  text-decoration: underline;
}
.factual-values-modal {
  margin-bottom: 20px;
}
.factual-values-modal__p {
  font-size: 18px;
  font-weight: 400;
  line-height: 25.2px;
  margin-right: 10px;
}
.factual-values-modal__p_blue {
  font-size: 16px;
  margin-right: 16px;
  cursor: pointer;
}
.factual-values-modal__p_blue:hover {
  text-decoration: underline;
}
.factual-values-modal-icon {
  font-size: 18px;
}
