import React, { useState } from "react";
import { HashRouter, Route, Link } from "react-router-dom";
import "../Tabs/Tabs.less";
import Documentation from "../Documentation/Documentation";
import Indicators from "../Indicators/Indicators";
import { path } from "../../../router";
import { useSelector, useDispatch } from "react-redux";
import { changeActiveTab, getActiveView } from "../../store/location";

export default function Tabs({ children }) {
  const getLastValueUrl = (url) => url.split("/").pop();

  const addsValue = getLastValueUrl(document.location.href);
  const firstLabel = addsValue === getLastValueUrl(`${path.npr.documentation}`) ? children[1].props.label : children[0].props.label;
  const [activeTab, setActiveTab] = useState(firstLabel);

  const handleClick = (newActiveTab) => {
    setActiveTab(newActiveTab);
  };

  const dispatch = useDispatch();

  const view = useSelector(getActiveView);

  return (
    <HashRouter>

      <Route path={`${path.main}`}>

        <div className="tabs">

          {children.map((tab) => {

            const label = tab.props.label;

            return (
              <div style={{ marginRight: "8px" }} key={label}
                onClick={() => {
                  dispatch(changeActiveTab(label))
                }}>

                <Link
                  to={label === `${path.npr.documentation}`.split("/").pop()
                    ? `${path.npr.main}/${label}` : `${path.npr.main}/${label}/${view}`}
                >

                  <button className={label === activeTab ? "u-tab u-tab-active" : "u-tab"}
                    onClick={() => handleClick(label)}>
                    {tab.props.title}
                  </button>

                </Link>

              </div>
            );
          })}

        </div>

      </Route>

      <Route exact path={[`${path.main}`, `${path.npr.main}`, `${path.npr.indicators}`, `${path.npr.indicatorsParams}`]}>
        <Indicators />
      </Route>

      <Route path={`${path.npr.documentation}`}>
        <Documentation />
      </Route>

    </HashRouter>
  );
}
