export const filterApi = (inputValue, closeNull, closeNotEditable, cards) => {

    const srtingToNormal = (str) => str?.toLowerCase().trim();

    return [...cards]
        .filter(card => (closeNull && !card.isNull || !closeNull) && (closeNotEditable && card.isEditable || !closeNotEditable))
        .filter(card => srtingToNormal(card.title)?.includes(srtingToNormal(inputValue)));
}