import React from "react";
import { Link } from "react-router-dom";
import "./IndicatorsCards.less";
import { path } from "../../../router";
import { useSelector } from "react-redux";
import { getPagStart, getPagEnd } from "../../store/pagination";

export default function IndicatorsCards({ cards }) {

    const pagStart = useSelector(getPagStart);
    const pagEnd = useSelector(getPagEnd);

    return (
        <>
            {
                cards
                    .slice(pagStart, pagEnd)
                    .map((card) => {
                        return (
                            <div className="indicators-cards-card bg-white" key={card.id}>
                                <Link to={`${path.npr.indicatorsCards}/${card.id}`} className="indicators-cards-card__title clr-blue-main">{card.title}</Link>
                                <p className="tt">К оплате: {card.amount}</p>
                            </div>
                        );
                    })
            }
        </>
    );
}