___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 666px) {
  /*для средних планшетов*/
  h2 {
    font-size: 24px;
    line-height: 28px;
  }
  h3 {
    font-size: 16px;
    line-height: 20px;
  }
  h4 {
    font-size: 14px;
    line-height: 18px;
  }
}
/*media queries*/
.modalcard__title {
  line-height: 33.6px;
}
.modalcard__data {
  display: flex;
  margin-top: 32px;
  margin-bottom: 32px;
}
.modalcard__data-col1 {
  width: 367px;
  margin-right: 16px;
}
.modalcard__data-a {
  cursor: pointer;
}
.modalcard_mb {
  margin-bottom: 16px;
}
.modalcard_open {
  animation: open 0.6s 1;
  animation-fill-mode: forwards;
}
.modalcard_hide {
  animation: hide 0.2s 1;
  animation-fill-mode: forwards;
}
@keyframes open {
  0% {
    max-height: 0;
    overflow: hidden;
  }
  100% {
    max-height: 1000px;
    overflow: visible;
  }
}
@keyframes hide {
  0% {
    max-height: 1000px;
    overflow: visible;
  }
  100% {
    max-height: 0;
    overflow: hidden;
  }
}
.modalcard-factual__heading {
  margin-top: 32px;
  margin-bottom: 21px;
}
.modalcard-factual-flex {
  display: flex;
  align-items: center;
}
.modalcard-factual-flex-space-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.modalcard-factual__p {
  font-size: 18px;
  font-weight: 400;
  line-height: 25.2px;
  margin-right: 10px;
}
.modalcard-factual__p_blue {
  font-size: 16px;
  margin-right: 16px;
  cursor: pointer;
}
.modalcard-factual__p_blue:hover {
  text-decoration: underline;
}
.modalcard-factual-message {
  margin-top: 20px;
}
.modalcard-factual__inputs {
  margin-top: 24px;
  margin-bottom: 32px;
}
.modalcard-factual-grid {
  display: grid;
}
.modalcard-factual-grid-1 {
  grid-template-columns: 4fr 2fr;
  grid-column-gap: 16px;
  margin-bottom: 16px;
}
.modalcard-factual-grid-2 {
  grid-template-columns: 5fr 1fr;
  grid-column-gap: 16px;
  margin-bottom: 16px;
}
.modalcard-factual-grid-3 {
  grid-template-columns: 2fr 4fr;
  grid-column-gap: 16px;
}
.modalcard-factual__label {
  display: block;
  font-size: 15px;
  font-weight: 400;
  color: #545454;
  margin-bottom: 4px;
}
.modalcard-factual-select1 {
  min-width: 30%;
}
.modalcard-factual-select2 {
  min-width: 68.5%;
}
.modalcard-factual__buttons {
  margin-bottom: 40px;
}
.modalcard-factual__button-save {
  padding: 0 34px !important;
  margin-left: 12px;
}
.modalcard-factual__button-agreement {
  padding: 0 21.5px !important;
}
.modalcard-factual__button-add {
  margin-top: 12px;
}
.modalcard-factual__stored-value {
  margin-bottom: 20px;
}
.modalcard-factual-icon {
  font-size: 18px;
}
