import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./Indicators.less";
import Selectbox from "../Selectbox";
import Pagination from "../Pagination/Pagination";
import { path } from "../../../router";
import { useSelector, useDispatch } from "react-redux";
import { getActiveView, changeActiveView } from "../../store/location";
import IndicatorsCards from "../indicatorsCards/IndicatorsCards";
import IndicatorsTable from "../indicatorsTable/IndicatorsTable";
import Modal from "../Modal";
import SelectboxPeriods from "../SelectboxPeriods";
import { cardsApi } from "../cardsApi";
import { useEffect } from "react";
import { filterApi } from "../FilterApi";
import { changePeriodId, getPeriodId } from "../../store/period"

export default function Indicators() {

    const [inputValue, setInputValue] = useState("");
    const [closeNull, setCloseNull] = useState(false);
    const [closeNotEditable, setCloseNotEditable] = useState(false);

    const [modalActive, setModalActive] = useState();

    const periods = [{ title: "2023, 01.01.2023 - 31.12.2023", value: 3 }, { title: "2022, 01.01.2022 - 31.12.2022", value: 2 }, { title: "2021, 01.01.2021 - 31.12.2021", value: 1 }];
    const periodId = useSelector(getPeriodId);
    const [cards, setCards] = useState([]);
    const [currentPeriod, setCurrentPeriod] = useState(periods.find((period) => period?.value === periodId));

    useEffect(() => {
        setCards(filterApi(inputValue, closeNull, closeNotEditable, cardsApi[periodId].cards));
        setCurrentPeriod(periods.find((period) => period?.value === periodId))
    }, [periodId, inputValue, closeNull, closeNotEditable])


    const onClickPeriod = (id) => {
        dispatch(changePeriodId(id));
    }

    const dispatch = useDispatch();

    const view = useSelector(getActiveView);

    const [activeTh, setActiveTh] = useState(
        {
            title: true, kindActivity: true, limitation: false,
            points: true, points_without_limits: false, transfer_points: false, amount: true
        }
    )

    const pageLimits = [5, 10, 15];
    const [pageLimit, setPageLimit] = useState(pageLimits[0]);

    return (
        <>
            <div className="indicators">
                <div className="indicators__card bg-white">
                    <div className="indicators__flex">

                        <h3 className="clr-blue-main">Всего баллов к оплате: {cardsApi[periodId].totals.amount}</h3>

                        <div className="indicators__flex">

                            <p className="clr-blue-main th indicators_tune" onClick={() => {
                                setModalActive(true);
                            }}>Настроить таблицу</p>

                            <Link to={`${path.npr.indicatorsTable}`} className="indicators-icon indicators-icon_mr"
                                onClick={() => {
                                    dispatch(changeActiveView(`${path.npr.indicatorsTable}`.split("/").pop()));
                                }}>

                                <i className={view !== `${path.npr.indicatorsCards}`.split("/").pop()
                                    ? "u-icon icon-table-view"
                                    : "u-icon icon-table-view indicators-icon_inactive"}></i>

                            </Link>

                            <Link to={`${path.npr.indicatorsCards}`} className="indicators-icon"
                                onClick={() => {
                                    dispatch(changeActiveView(`${path.npr.indicatorsCards}`.split("/").pop()));
                                }}
                            >

                                <i className={view === `${path.npr.indicatorsCards}`.split("/").pop()
                                    ? "u-icon icon-list-view indicators-icon indicators-icon_active"
                                    : "u-icon icon-list-view indicators-icon"}></i>

                            </Link>
                        </div>
                    </div>

                    <div className="indicators__description tt">

                        <p>Баллы с ограничениями и коэффициентами: <span className="clr-blue-main">{cardsApi[periodId].totals.withCoef}</span></p>
                        <p>Баллы без ограничений: <span className="clr-blue-main">{cardsApi[periodId].totals.total}</span></p>

                    </div>

                    <p className="indicators__select-label">Отчетный период</p>

                    <div className="indicators__grid-columns">

                        <SelectboxPeriods
                            periods={periods}
                            currentPeriod={currentPeriod}
                            func={onClickPeriod} />

                        {/* <Selectbox optionsArr={["2021, 01.01.2023 - 31.12.2023", "2022, 01.01.2022 - 31.12.2022", "2023, 01.01.2023 - 31.12.2023"]} 
                        placeholder="Выбрать" /> */}

                        <div className="u-search indicators__search">
                            <input placeholder="Введите название показателя" className="u-input" type="search" name="" id="" value={inputValue} onChange={
                                (e) => { setInputValue(e.target.value) }} />
                            <button className="u-search-loupe" type="button"></button>
                        </div>

                    </div>

                    <div className="flex-container">
                        <label className="u-checkbox indicators__checkbox clr-gray-40">
                            <input type="checkbox"
                                onClick={() => { setCloseNull(!closeNull); }} />
                            <div></div>
                            скрыть нулевые значения
                        </label>
                        <label className="u-checkbox indicators__checkbox clr-gray-40">
                            <input type="checkbox"
                                onClick={() => {
                                    setCloseNotEditable(!closeNotEditable);
                                }} />
                            <div></div>
                            скрыть не редактируемые значения
                        </label>
                    </div>
                </div>

                <div className={view !== `${path.npr.indicatorsCards}`.split("/").pop() ? "" : "indicators__hide"}>
                    <IndicatorsTable inputValue={inputValue} closeNull={closeNull} closeNotEditable={closeNotEditable} activeTh={activeTh} cards={cards} />
                </div>
                <div className={view === `${path.npr.indicatorsCards}`.split("/").pop() ? "" : "indicators__hide"}>
                    <IndicatorsCards inputValue={inputValue} closeNull={closeNull} closeNotEditable={closeNotEditable} cards={cards} />
                </div>

                <Pagination inputValue={inputValue} closeNull={closeNull} closeNotEditable={closeNotEditable} pageLimit={pageLimit} setPageLimit={setPageLimit} pageLimits={pageLimits} cards={cards}></Pagination>
            </div>
            <Modal active={modalActive} setActive={setModalActive}>
                <h3 className="clr-blue-main">Настройка вида таблицы</h3>
                <div className="u-table indicators_tune_table">
                    <table className="table">
                        <thead>
                            <tr>
                                <th>
                                    <p>Отображение</p>
                                </th>
                                <th>
                                    <p>Столбец таблицы</p>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>
                                    <label className="u-checkbox">
                                        <input type="checkbox" id="title" checked={activeTh.title} onChange={() => {
                                            setActiveTh({ ...activeTh, title: !activeTh.title });
                                        }} />
                                        <div></div>
                                    </label>
                                </td>
                                <td>
                                    <label htmlFor="title">
                                        Наименование показателя
                                    </label>
                                </td>
                            </tr>
                            <tr>
                                <td>

                                    <label className="u-checkbox">
                                        <input type="checkbox" id="kindActivity" checked={activeTh.kindActivity} onChange={() => {
                                            setActiveTh({ ...activeTh, kindActivity: !activeTh.kindActivity });
                                        }} />
                                        <div></div>
                                    </label>

                                </td>
                                <td>
                                    <label htmlFor="kindActivity">
                                        Вид деятельности
                                    </label>
                                </td>
                            </tr>
                            <tr>
                                <td>

                                    <label className="u-checkbox">
                                        <input type="checkbox" id="limitation" checked={activeTh.limitation} onChange={() => {
                                            setActiveTh({ ...activeTh, limitation: !activeTh.limitation });
                                        }} />
                                        <div></div>
                                    </label>

                                </td>
                                <td>
                                    <label htmlFor="limitation">
                                        Ограничение
                                    </label>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <label className="u-checkbox">
                                        <input type="checkbox" id="points" checked={activeTh.points} onChange={() => {
                                            setActiveTh({ ...activeTh, points: !activeTh.points });
                                        }} />
                                        <div></div>
                                    </label>
                                </td>
                                <td>
                                    <label htmlFor="points">
                                        Баллы
                                    </label>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <label className="u-checkbox">
                                        <input type="checkbox" id="points_without_limits" checked={activeTh.points_with_limits} onChange={() => {
                                            setActiveTh({ ...activeTh, points_with_limits: !activeTh.points_with_limits });
                                        }} />
                                        <div></div>
                                    </label>
                                </td>
                                <td>
                                    <label htmlFor="points_without_limits">
                                        Баллы, с учетом ограничений
                                    </label>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <label className="u-checkbox">
                                        <input type="checkbox" id="transfer_points" checked={activeTh.transfer_points} onChange={() => {
                                            setActiveTh({ ...activeTh, transfer_points: !activeTh.transfer_points });
                                        }} />
                                        <div></div>
                                    </label>
                                </td>
                                <td>
                                    <label htmlFor="transfer_points">
                                        Передача баллов
                                    </label>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <label className="u-checkbox">
                                        <input type="checkbox" id="amount" checked={activeTh.amount} onChange={() => {
                                            setActiveTh({ ...activeTh, amount: !activeTh.amount });
                                        }} />
                                        <div></div>
                                    </label>
                                </td>
                                <td>
                                    <label htmlFor="amount">
                                        К оплате
                                    </label>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div className="indicators_tune__flex">
                    <p>Отображать по</p>
                    <div className="indicators_tune_selectbox">
                        <Selectbox optionsArr={pageLimits} hasCross={false} pageLimit={pageLimit} setPageLimit={setPageLimit} isPages={true} />
                    </div>
                    <p>записей на странице</p>
                </div>
            </Modal>
        </>
    );
}