import { CHANGE_PERIOD_ID } from "./actions";

export const periodInitialState = {
    periodId: 3,
}

export const periodReducer = (state = periodInitialState, action) => {
    switch (action.type) {
        case CHANGE_PERIOD_ID: return {
            ...state,
            periodId: action.payload
        }
        default: { return state; }
    }
}