import { CHANGE_PAG_START, CHANGE_PAG_END } from "./actions";

export const paginationInitialState = {
    start: 0,
    end: 20
}

export const paginationReducer = (state = paginationInitialState, action) => {
    switch (action.type) {
        case CHANGE_PAG_START: return {
            ...state,
            start: action.payload
        }
        case CHANGE_PAG_END: return {
            ...state,
            end: action.payload
        }
        default: { return state; }
    }
}