  /*для средних планшетов*/
  h2 {
    font-size: 24px;
    line-height: 28px;
  }
  h3 {
    font-size: 16px;
    line-height: 20px;
  }
  h4 {
    font-size: 14px;
    line-height: 18px;
  }
}
/*media queries*/
.indicatorcard {
  margin-bottom: 6px;
}
.indicatorcard__h2 {
  margin-top: 6px;
}
.indicatorcard__reporting-period {
  margin-top: 17px;
}
.indicatorcard__reporting-period-span {
  margin-left: 10px;
  color: #7A7A7A;
}
.indicatorcard__card {
  border-radius: 10px;
  padding: 50px 32px 32px 50px;
  max-width: 1012px;
  margin-top: 32px;
}
.indicatorcard__card-title {
  font-size: 32px;
  font-weight: 600;
  line-height: 35px;
}
.indicatorcard__card-content {
  margin-top: 50px;
}
.indicatorcard__card-factual {
  margin-top: 32px;
  padding: 32px 50px;
}
.indicatorcard__card-agreement {
  margin-top: 16px;
  padding: 26px 32px;
}
.indicatorcard__section-title {
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 16px;
  cursor: pointer;
}
.indicatorcard__section-mb {
  margin-bottom: 32px;
}
.indicatorcard-icon {
  font-size: 12px;
  margin-left: 10px;
  cursor: pointer;
}
.indicatorcard_open {
  animation: open 0.6s 1;
  animation-fill-mode: forwards;
}
.indicatorcard_hide {
  animation: hide 0.2s 1;
  animation-fill-mode: forwards;
}
@keyframes open {
  0% {
    max-height: 0;
    overflow: hidden;
  }
  100% {
    max-height: 1000px;
    overflow: visible;
  }
}
@keyframes hide {
  0% {
    max-height: 1000px;
    overflow: visible;
  }
  100% {
    max-height: 0;
    overflow: hidden;
  }
}
.indicatorcard__col {
  margin-left: 50px;
}
.indicatorcard__data {
  margin-bottom: 32px;
}
.indicatorcard__other-elements {
  width: 180px;
}
.indicatorcard__other-elements-heading {
  margin-bottom: 15px;
  cursor: pointer;
}
.indicatorcard__other-elements-text {
  margin-bottom: 12px;
}
.indicatorcard__other-elements-text:last-child {
  margin-bottom: 0;
}
.indicatorcard__other-elements-a {
  cursor: pointer;
}
.indicatorcard__other-elements-inactive:before {
  color: #545454 !important;
}
.indicatorcard-factual__heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 26px;
  margin-bottom: 20px;
}
.indicatorcard-factual-flex {
  display: flex;
  align-items: center;
}
.indicatorcard-factual-flex-space-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.indicatorcard-factual__p {
  font-size: 24px;
  font-weight: 400;
  margin-right: 10px;
}
.indicatorcard-factual__p_blue {
  margin-right: 16px;
  cursor: pointer;
}
.indicatorcard-factual__p_blue:hover {
  text-decoration: underline;
}
.indicatorcard-factual__inputs {
  margin-top: 32px;
  margin-bottom: 40px;
}
.indicatorcard-factual-grid {
  display: grid;
}
.indicatorcard-factual-grid-1 {
  grid-template-columns: 4fr 2fr;
  grid-column-gap: 16px;
  margin-bottom: 24px;
}
.indicatorcard-factual-grid-2 {
  grid-template-columns: 5fr 1fr;
  grid-column-gap: 16px;
  margin-bottom: 24px;
}
.indicatorcard-factual-grid-3 {
  grid-template-columns: 2fr 4fr;
  grid-column-gap: 16px;
}
.indicatorcard-factual__label {
  display: block;
  font-size: 15px;
  font-weight: 400;
  color: #545454;
  margin-bottom: 4px;
}
.indicatorcard-factual-button-save {
  padding: 0 34px !important;
  margin-left: 12px;
}
.indicatorcard-factual-button-agreement {
  padding: 0 21.5px !important;
}
.indicatorcard-factual-select1 {
  min-width: 30%;
}
.indicatorcard-factual-select2 {
  min-width: 68.5%;
}
.indicatorcard-factual-icon {
  font-size: 20px;
}
@media screen and (max-width: 1440px) {
  .indicatorcard {
    margin: 0 70px 6px;
  }
}
