___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 666px) {
  /*для средних планшетов*/
  h2 {
    font-size: 24px;
    line-height: 28px;
  }
  h3 {
    font-size: 16px;
    line-height: 20px;
  }
  h4 {
    font-size: 14px;
    line-height: 18px;
  }
}
/*media queries*/
.indicators__card {
  border-radius: 10px;
  padding: 32px;
  max-width: 1012px;
  margin-bottom: 16px;
}
.indicators__card:last-child {
  margin-bottom: 0;
}
.indicators__flex {
  display: flex;
  justify-content: space-between;
  margin-bottom: 16px;
}
.indicators_tune {
  margin-right: 33px;
  cursor: pointer;
}
.indicators_tune:hover {
  text-decoration: underline;
}
.indicators_tune_table {
  font-size: 14px;
  border: 1px solid #1E4391;
  margin-top: 16px;
}
.indicators_tune_table label {
  cursor: pointer;
}
.indicators_tune_table th:first-child {
  text-align: center;
}
.indicators_tune_table td:first-child label {
  margin-left: 50%;
}
.indicators_tune_selectbox {
  width: 92px;
  margin: 0 16px;
}
.indicators_tune__flex {
  display: flex;
  align-items: center;
  margin-top: 18px;
}
.indicators-icon {
  display: flex;
  font-size: 20px;
  cursor: pointer;
}
.indicators-icon:hover {
  text-decoration: none;
}
.indicators-icon_active:before {
  color: #1E4391 !important;
}
.indicators-icon_inactive:before {
  color: #7A7A7A !important;
}
.indicators-icon_mr {
  margin-right: 16px;
}
.indicators__description {
  margin-bottom: 16px;
}
.indicators__select-label {
  font-size: 15px;
  color: #7A7A7A;
  margin-bottom: 4px;
}
.indicators__grid-columns {
  display: grid;
  grid-template-columns: 1fr 2fr;
}
.indicators__search {
  margin-left: 16px;
}
.indicators__checkbox {
  font-weight: 400;
  font-size: 12px;
  line-height: 16.8px;
  display: flex;
  cursor: pointer;
  margin: 16px 32px 0 0;
}
.indicators__checkbox div {
  margin-right: 6px;
}
.indicators__hide {
  display: none;
}
