import React, { useEffect, useState } from "react";
import "./Pagination.less";
import { useSelector, useDispatch } from "react-redux";
import { getPagStart, getPagEnd, changePagStart, changePagEnd } from "../../store/pagination";

const leftSeparator = "left";
const rightSeparator = "right";

export default function Pagination({ pageLimit, setPageLimit, pageLimits, cards }) {

    const dispatch = useDispatch();
    const pagStart = useSelector(getPagStart);
    const pagEnd = useSelector(getPagEnd);

    const [activePage, setActivePage] = useState(1);

    const pagesCount = Math.ceil(cards.length / pageLimit) || 1;

    if (activePage > pagesCount) {
        setActivePage(pagesCount);
    }
    if (activePage < 1) {
        setActivePage(1);
    }

    useEffect(() => {
        dispatch(changePagStart((activePage - 1) * pageLimit));
    }, [dispatch, activePage, pageLimit]);

    useEffect(() => {
        dispatch(changePagEnd(pagStart + pageLimit));
    }, [dispatch, pagStart, pageLimit]);

    const range = (from, to, step = 1) => {
        let i = from;
        const range = [];

        while (i <= to) {
            range.push(i);
            i += step;
        }
        return range;
    };

    const getPagesNumbers = () => {
        if (pagesCount > 8) {
            let pages = [];

            const leftBound = activePage - 2;
            const rightBound = activePage + 1;

            const beforeLastPage = pagesCount - 1;

            const startPage = leftBound > 2 ? leftBound : 2;
            const endPage = rightBound < beforeLastPage ? rightBound : beforeLastPage;

            pages = range(startPage, endPage);

            const singleSpillOffset = 5 - pages.length;

            const leftSpill = startPage > 2;
            const rightSpill = endPage < beforeLastPage;

            if (leftSpill && !rightSpill) {
                const extraPages = range(startPage - singleSpillOffset, startPage - 1);
                pages = [leftSeparator, ...extraPages, ...pages];
            }
            else if (!leftSpill && rightSpill) {
                const extraPages = range(endPage + 1, endPage + singleSpillOffset);
                pages = [...pages, ...extraPages, rightSeparator];
            }
            else if (leftSpill && rightSpill) {
                pages = [leftSeparator, ...pages, rightSeparator];
            }

            return [1, ...pages, pagesCount];
        }
        return range(1, pagesCount);
    };

    const pagesNumbers = getPagesNumbers();

    return (
        <>
            <div className="u-pagination pagination">
                <p className="u-pagination__result">Записи с {pagStart + 1} по {pagEnd < cards.length ? pagEnd : cards.length} из {cards.length}</p>
                <div className="u-pagination__limits">
                    Выводить по
                    <div className="u-pagination__limit-wrapper">
                        {pageLimits.map((limit) => {
                            return <span className={limit == pageLimit
                                ? "u-pagination__limit_active"
                                : "u-pagination__limit"}
                                key={limit}
                                onClick={() => {
                                    setPageLimit(limit);
                                }}>{limit}</span>
                        })}
                    </div>
                </div>

                <div className="u-paginator">

                    <div className={activePage === 1
                        ? "u-paginator__arrow-btn u-paginator__arrow-btn_inactive"
                        : "u-paginator__arrow-btn"}
                        onClick={() => {
                            setActivePage(activePage - 1)
                        }}></div>

                    <div className="u-paginator__pages">

                        {
                            pagesNumbers.map((pageNum) => {
                                if (pageNum === leftSeparator) {
                                    return <div key="left">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="9" height="3" viewBox="0 0 9 3" fill="none">
                                            <path d="M1.61584 2.07C1.4105 2.07 1.23317 1.99533 1.08384 1.846C0.943836 1.69667 0.873836 1.51467 0.873836 1.3C0.873836 1.076 0.943836 0.894 1.08384 0.754C1.23317 0.614 1.4105 0.544 1.61584 0.544C1.82117 0.544 1.99384 0.614 2.13384 0.754C2.28317 0.894 2.35784 1.076 2.35784 1.3C2.35784 1.51467 2.28317 1.69667 2.13384 1.846C1.99384 1.99533 1.82117 2.07 1.61584 2.07ZM4.58263 2.07C4.3773 2.07 4.19997 1.99533 4.05063 1.846C3.91063 1.69667 3.84063 1.51467 3.84063 1.3C3.84063 1.076 3.91063 0.894 4.05063 0.754C4.19997 0.614 4.3773 0.544 4.58263 0.544C4.78797 0.544 4.96063 0.614 5.10063 0.754C5.24997 0.894 5.32463 1.076 5.32463 1.3C5.32463 1.51467 5.24997 1.69667 5.10063 1.846C4.96063 1.99533 4.78797 2.07 4.58263 2.07ZM7.54943 2.07C7.3441 2.07 7.16676 1.99533 7.01743 1.846C6.87743 1.69667 6.80743 1.51467 6.80743 1.3C6.80743 1.076 6.87743 0.894 7.01743 0.754C7.16676 0.614 7.3441 0.544 7.54943 0.544C7.75476 0.544 7.92743 0.614 8.06743 0.754C8.21676 0.894 8.29143 1.076 8.29143 1.3C8.29143 1.51467 8.21676 1.69667 8.06743 1.846C7.92743 1.99533 7.75476 2.07 7.54943 2.07Z" fill="#545454" />
                                        </svg>
                                    </div>
                                }
                                if (pageNum === rightSeparator) {
                                    return <div key="right">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="9" height="3" viewBox="0 0 9 3" fill="none">
                                            <path d="M1.61584 2.07C1.4105 2.07 1.23317 1.99533 1.08384 1.846C0.943836 1.69667 0.873836 1.51467 0.873836 1.3C0.873836 1.076 0.943836 0.894 1.08384 0.754C1.23317 0.614 1.4105 0.544 1.61584 0.544C1.82117 0.544 1.99384 0.614 2.13384 0.754C2.28317 0.894 2.35784 1.076 2.35784 1.3C2.35784 1.51467 2.28317 1.69667 2.13384 1.846C1.99384 1.99533 1.82117 2.07 1.61584 2.07ZM4.58263 2.07C4.3773 2.07 4.19997 1.99533 4.05063 1.846C3.91063 1.69667 3.84063 1.51467 3.84063 1.3C3.84063 1.076 3.91063 0.894 4.05063 0.754C4.19997 0.614 4.3773 0.544 4.58263 0.544C4.78797 0.544 4.96063 0.614 5.10063 0.754C5.24997 0.894 5.32463 1.076 5.32463 1.3C5.32463 1.51467 5.24997 1.69667 5.10063 1.846C4.96063 1.99533 4.78797 2.07 4.58263 2.07ZM7.54943 2.07C7.3441 2.07 7.16676 1.99533 7.01743 1.846C6.87743 1.69667 6.80743 1.51467 6.80743 1.3C6.80743 1.076 6.87743 0.894 7.01743 0.754C7.16676 0.614 7.3441 0.544 7.54943 0.544C7.75476 0.544 7.92743 0.614 8.06743 0.754C8.21676 0.894 8.29143 1.076 8.29143 1.3C8.29143 1.51467 8.21676 1.69667 8.06743 1.846C7.92743 1.99533 7.75476 2.07 7.54943 2.07Z" fill="#545454" />
                                        </svg>
                                    </div>
                                }
                                return <div className={pageNum === activePage ? "u-paginator__pages-num u-paginator__pages-num_active" : "u-paginator__pages-num"}
                                    onClick={() => { setActivePage(pageNum) }}
                                    key={pageNum}>{pageNum}</div>
                            })
                        }

                    </div>

                    <div className={activePage === pagesCount
                        ? "u-paginator__arrow-btn u-paginator__arrow-btn-right u-paginator__arrow-btn-right_inactive"
                        : "u-paginator__arrow-btn u-paginator__arrow-btn-right"}
                        onClick={() => {
                            setActivePage(activePage + 1)
                        }}></div>

                </div>
            </div>
        </>
    )
}
