import React, { useState, useEffect } from "react";

export default function SelectboxPeriods({ periods, func, currentPeriod }) {

    const [open, setOpen] = useState(false);
    const [value, setValue] = useState(currentPeriod);

    const options = periods?.map((period) => {
        return <div className="u-selectbox-option" key={period.value}
            onClick={() => { setValue(period); func(period.value) }}>{period.title}</div>;
    });

    useEffect(() => {
        document.addEventListener('click', handleClick)
        return () => document.removeEventListener('click', handleClick)
    }, [])

    const handleClick = (e) => {
        if (!e.target.closest('.u-selectbox')) {
            setOpen(false);
        }
    }

    return (
        <>
            <div className="u-selectbox">
                <div className={open ? "u-selectbox-select u-selectbox-select-open" : "u-selectbox-select"}

                    onClick={() => {
                        setOpen(!open);
                    }}>

                    <span className="u-selectbox-select-title">{value.title}</span>

                    <i className="u-selectbox-select-icon-arrow"></i>

                </div>

                <div className="u-selectbox-options"
                    onClick={() => {
                        setOpen(false);
                    }}>
                    {options}
                </div>

            </div>
        </>
    )
}
