import React from "react";
import { Link } from "react-router-dom";
import "./Menu.less";
import { path } from "../../../router";
import { useSelector } from "react-redux";
import { getActiveTab, getActiveView } from "../../store/location";

export default function Menu() {

    const tab = useSelector(getActiveTab);
    const view = useSelector(getActiveView);

    const ykvdActive = document.location.href.includes(`${path.ykvd}`);

    return (
        <div className="menu">
            <h4 className="clr-blue-main">Мои показатели</h4>
            <ul className="menu__ul">
                <li>
                    <Link to={tab === `${path.npr.documentation}`.split("/").pop()
                        ? `${path.npr.main}/${tab}` : `${path.npr.main}/${tab}/${view}`}
                        className={ykvdActive ? "menu__a" : "menu__a menu__a_active"}>
                        <div className={ykvdActive ? "menu__database" : "menu__database menu__database_active"}></div>
                        НПР</Link>
                </li>
                <li>
                    <Link to={`${path.ykvd}`} className={ykvdActive ? "menu__a menu__a_active" : "menu__a "}>
                        <i className={ykvdActive
                            ? "u-icon icon-document menu-icon"
                            : "u-icon icon-document menu-icon menu-icon-inactive"}></i>
                        ЭКВД</Link>
                </li>
            </ul>
        </div>
    );
}
