import React from "react";
import { Link } from "react-router-dom";
import "./NPR.less";
import Tabs from "./../Tabs/Tabs";
import { path } from "../../../router";
import { useSelector } from "react-redux";
import { getActiveTab, getActiveView } from "../../store/location";

export default function NPR() {

  const tab = useSelector(getActiveTab);
  const view = useSelector(getActiveView);

  return (
    <div className="npr">

      <div className="u-bc">

        <Link to={tab === `${path.npr.documentation}`.split("/").pop()
          ? `${path.npr.main}/${tab}`
          : `${path.npr.main}/${tab}/${view}`}
          className="u-bc-link">Главная</Link>

        <Link to={tab === `${path.npr.documentation}`.split("/").pop()
          ? `${path.npr.main}/${tab}`
          : `${path.npr.main}/${tab}/${view}`}
          className="u-bc-link">Система стимулирования НПР</Link>

        <a className="u-bc-last">Мои показатели</a>

      </div>

      <h2 className="npr__h2 clr-blue-main">Система стимулирования НПР</h2>

      <Tabs>
        <div title="Мои показатели" label={`${path.npr.indicators}`.split("/").pop()} />
        <div title="Документация" label={`${path.npr.documentation}`.split("/").pop()} />
      </Tabs>

    </div>
  );
}
